@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.page {
  min-height: 100vh;
  max-width: 100%;
  display: flex;
  justify-content: center;
  background: linear-gradient(45deg, #e6f5ff 45%, #6bbeff 85%);
}

.special-table {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px 10px 0;
}
.special-table .special-table__columns {
  display: flex;
  max-width: 1260px;
  width: 100%;
  flex-wrap: wrap;
  margin-bottom: 15px;
  justify-content: center;
}
.special-table .special-table__save-button {
  max-width: 40%;
  border: none;
  font: 'Roboto', sans-serif;
  font-weight: 700;
  font-size: 20px;
  padding: 8px 9px;
  background-color: #fff;
  border-radius: 0.5rem;
  cursor: pointer;
}
.special-table .special-table__save-button:hover {
  background-color: #ccecff;
}
.special-table .special-table__save-button:active {
  cursor: none;
  background-color: #cacaca;
}

.special-column {
  display: flex;
  flex-direction: column;
  max-width: 400px;
  width: 100%;
  margin: 0 10px 15px;
}
@-webkit-keyframes increase {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.05, 1.03);
  }
}
@keyframes increase {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.05, 1.03);
  }
}
.special-column__control-block {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-left: 5px;
  padding-right: 5px;
}
.special-column__control-block input {
  display: flex;
  max-width: 300px;
  width: 90%;
  box-sizing: border-box;
  margin: 0 auto 10px;
  padding: 4px 10px;
  border: none;
  border-radius: 0.4rem;
  height: 34px;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  background-color: rgba(255, 255, 255, 0.7);
  color: #000;
}
.special-column__control-block button {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 40%;
  width: 100%;
  margin-bottom: 10px;
  border: none;
  border-radius: 0.3rem;
  height: 30px;
  font-family: 'Roboto', sans-serif;
  font-size: 15px;
  font-weight: 500;
  background-color: #fff;
}
.special-column__control-block button:hover {
  -webkit-animation: increase 0.15s ease-in forwards;
          animation: increase 0.15s ease-in forwards;
  cursor: pointer;
}
.special-column__control-block button:active {
  background-color: #dfdfdf;
  cursor: none;
}
.special-column__cells-block {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.special-column__cells-block .special-column__cells-block_button {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  margin-bottom: 2px;
  border: none;
  background: none;
  font: 'Roboto', sans-serif;
  font-weight: 700;
  font-size: 18px;
  cursor: pointer;
}
.special-column__cells-block ul {
  list-style: none;
  padding: 0;
  margin: 0 0 6px;
  width: 100%;
}

.special-cell {
  width: 100%;
  margin-bottom: 4px;
  display: flex;
}
.special-cell .special-cell__input {
  width: 100%;
  border: none;
  box-sizing: border-box;
  height: 30px;
  margin: 0;
  padding: 0 0 0 5px;
  font-size: 16px;
}
.special-cell .special-cell__del-button {
  width: 30px;
  height: 30px;
  margin: 0;
  background-image: url(/panda-react-less/static/media/cross.06bd4fba.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-color: #ff5757;
  border: none;
  cursor: pointer;
}
.special-cell .special-cell__del-button:hover {
  background-color: #ff3131;
}

.pagination {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.pagination .pagination__button {
  border: none;
  width: 30px;
  height: 20px;
  font-weight: 700;
  background-color: #ffffff;
  cursor: pointer;
}
.pagination .pagination__button:first-of-type {
  border-radius: 1rem 8px 8px 1rem;
}
.pagination .pagination__button:last-of-type {
  border-radius: 8px 1rem 1rem 8px;
}
.pagination .pagination__page-number {
  margin: 0 5px;
  font: 'Roboto', sans-serif;
  font-weight: 700;
  font-size: 17px;
}

